import React from 'react';
import BCorpLogo from '../assets/bcorp-logo.png';
import MoreBtn from './MoreBtn.jsx';
import '../scss/styles.scss';
import '../../tasks/sheetData.js';
import { CSSTransitionGroup } from 'react-transition-group';



class BCorp extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			isToggleOn: true,
			display: 'none'
		}
		this.onMoreClick = this.onMoreClick.bind(this);

	}

	onMoreClick() {
		//this.setState({ moreBtnText: '< less' });
		//this.setState({ moreDisplay: 'block' })
		this.setState(prevState => ({
      		isToggleOn: !prevState.isToggleOn
    	}));
		if (this.state.isToggleOn == true) { this.setState({ display: 'block' }) }
		else { this.setState({ display: 'none' }) }
	}

	render() {
		return (
			<div>
				<div className="bcorp-container">
					<div className="bcorp-width">
						<a className="bcorp-logo" href='https://www.bcorporation.net/' target='_blank'><img src={BCorpLogo} alt="Logo" /></a>
						<div className="bcorp-vertical-line"></div>
						<div className="bcorp-info">
							<div className="bcorp-top-content"><h3> We're deeply committed to the nonprofit sector. <br /><br /> In fact, we're undergoing the rigorous standards to earn B Corporation certification.</h3></div>
							<div onClick={this.onMoreClick.bind(this)}><MoreBtn /> </div>
						</div>
					</div>
					<div className="bcorp-more-content" key={'bcorp-more'} style={{ display: this.state.display }}><h4> CohenTaylor is being certified by nonprofit B Lab as a B Corporation. Because we believe that successful business is more than profit margins, our values of sustainability, honesty, and transparancy lead our day-to day work and have allowed us to apply for this certification. We are a force for good, and our B Corp status reinforces that founding principle. Click on the logo to the left to learn more. </h4></div>
				</div>
			</div>
		)
	}
}

export default BCorp;


